
@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap');


.wrapper {
   display: flex;
   flex-direction: column;
}

 .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-size: 40px;    
    background: LightGray;
  }  
    
  .content{
      flex-grow: 3;
       background:#FFFFFF;
       flex-wrap: wrap;
    }

  .sidebar { 
      min-width: 30%;
      flex-grow: 0.3;
      display: flex; 
      flex-direction: column;
      align-items: center;
      background: url("https://artofcici-img.s3.amazonaws.com/side.jpg") no-repeat;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
      justify-content: space-between;
      align-items: flex-end;
      flex-wrap: wrap;
     
    }

    .name {     
      margin-top: 3rem; 
      padding: 0 1rem;
      font-family: 'Homemade Apple', cursive, sans-serif;
      color: white;
      font-size: 3rem;
      text-align: center;

    }

    .city {
      margin-bottom: 10rem;
      padding: 0 1rem;
      align-self: flex-end;
      text-align: center;
      font-family: 'Homemade Apple', cursive, sans-serif;
      color: white;
      font-size: 2rem;          
    }

    .footer {
      font-size: 40px;
      background: #8C2155;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: flex-end;
      height: 50vh;
    }
  

      .contact {       
      margin-bottom: 2rem;
      margin-right: 2rem;
      font-family: 'Homemade Apple', cursive, sans-serif;
      color: white;
      font-size: 2.5rem;  
     
      /* margin-top: 14rem;
      margin-right: 5rem; */
    }

    .gallery {       
      margin-bottom: 2rem;
      margin-right: 3rem;
      font-family: 'Homemade Apple', cursive, sans-serif;
      color: white;
      font-size: 2.5rem;  
      /* margin-top: 14rem;
      margin-right: 5rem; */
    }


    /* Media Queries */

    @media screen and (max-width: 1050px) {
    
      .city {
        padding: .75rem;  
        text-align: center;   
        font-size: 1rem;
      }
    }


    @media screen and (max-width: 600px) {

      .sidebar { 
        min-width: none;
       
      }
      .name {
        padding: .75rem;  
        text-align: center;   
        font-size: 1.5rem;
      }

      .city {
        padding: .75rem;  
        text-align: center;   
        font-size: 1rem;
      }
    }


    @media screen and (max-width: 450px) {
      .name {
        padding: .75rem;  
        text-align: center;   
        font-size: 1.5rem;
      }

      .city {
        display: none;
       
      }
    }